<template>
  <v-container class="d-flex flex-wrap centercontainer">
    <v-text-field
      v-model="search"
      append-icon="mdi-magnify"
      label="Search"
      single-line
      hide-details
      @keyup.enter="searchEmployee()"
    ></v-text-field>
    <v-dialog v-model="dialog" max-width="80%">
      <v-card>
        <v-toolbar>
          <v-btn icon dark @click="$emit('changeDialog')">
            <v-icon color="black">mdi-close</v-icon>
          </v-btn>
          <v-toolbar-title>Person gefunden </v-toolbar-title>
        </v-toolbar>
        <v-list subheader>
          <v-subheader>Mitarbeiter </v-subheader>
          <v-row>
            <v-col>
              <v-list-item>
                <v-list-item-content>
                  <v-list-item-title>Vor -und Nachname</v-list-item-title>
                  <v-list-item-subtitle
                    >{{ employee.last_name }}
                    {{ employee.first_name }}</v-list-item-subtitle
                  >
                </v-list-item-content>
              </v-list-item>
            </v-col>
            <v-col>
              <v-list-item>
                <v-list-item-content>
                  <v-list-item-title>Geburtsdatum</v-list-item-title>
                  <v-list-item-subtitle
                    >({{
                      employee.gebdatum.substring(0, 10)
                    }})</v-list-item-subtitle
                  >
                </v-list-item-content>
              </v-list-item>
            </v-col>
          </v-row>
          <v-row>
            <v-col
              ><v-list-item>
                <v-list-item-content>
                  <v-list-item-title>Email</v-list-item-title>
                  <v-list-item-subtitle>{{
                    employee.email
                  }}</v-list-item-subtitle>
                </v-list-item-content>
              </v-list-item>
            </v-col>
            <v-col>
              <v-list-item>
                <v-list-item-content>
                  <v-list-item-title>Mitarbeiter-Typ</v-list-item-title>
                  <v-list-item-subtitle>{{
                    employee.typ
                  }}</v-list-item-subtitle>
                </v-list-item-content>
              </v-list-item>
            </v-col>
          </v-row>
        </v-list>
        <v-list subheader>
          <v-subheader>Aktivitäten </v-subheader>
          <v-row>
            <v-col>
              <v-list-item>
                <v-list-item-content>
                  <v-list-item-title
                    >Anzahl an Tests abgesendet</v-list-item-title
                  >
                  <v-list-item-subtitle
                    >{{ employee.anzahl }}
                  </v-list-item-subtitle>
                </v-list-item-content>
              </v-list-item>
            </v-col>
            <v-col>
              <v-list-item>
                <v-list-item-content>
                  <v-list-item-title>Negative Tests</v-list-item-title>
                  <v-list-item-subtitle>{{
                    employee.anzahl
                  }}</v-list-item-subtitle>
                </v-list-item-content>
              </v-list-item>
            </v-col>
          </v-row>
        </v-list>
        <v-divider></v-divider>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="blue darken-1"
            text
            class="white--text"
            @click="dialog = false"
          >
            Fertig
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import axios from 'axios';
export default {
  data() {
    return {
      search: '',
      serveraddress: process.env.VUE_APP_SERVER,
      employee: {},
      dialog: false,
    };
  },
  methods: {
    async searchEmployee() {
      const { data } = await axios({
        url: `${this.serveraddress}/activities/${this.search}`,
        method: 'GET',
      });
      this.employee = data[0];
      console.log(data);
      this.dialog = true;
    },
  },
};
</script>

<style lang="scss" scoped>
.centercontainer {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;

  height: 100%;
}
</style>
